* {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: #E8E8E8;
}

body {
  padding: 60px;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;

}

.card-grid {
  display: flex;
  flex-direction:row ;
  flex-wrap: wrap;

}

.card {

  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p  {
  padding: 5px;
  text-align: center;

}


.card img {
  max-height: 170px;
}

.title h2 {
  text-align: center;
  border: 2px solid black ;
  background-color: gray;
}

.title h4 {
  color: blue;
}